<template>
  <v-container class="py-8">
    <v-card class="pa-4" elevation="2">
      <h2 class="mb-4">Информация о запуске</h2>

      <v-list density="compact">

        <v-list-item>
          <v-list-item-title><strong>URL:</strong> {{ launchUrl }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>URL id:</strong> {{ urlId ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Бот:</strong> {{ bot ?? 'данные отсутствуют' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>platform:</strong> {{ platform ?? 'данные отсутствуют' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>version:</strong> {{ version ?? 'данные отсутствуют' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>ID пользователя:</strong> {{ user?.id ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Имя:</strong> {{ user?.first_name ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Телефон:</strong> {{ phoneText }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Идентификатор чата:</strong> {{ chat?.id ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>	Тип чата:</strong> {{ chat?.type ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>initDataUnsafe:</strong></v-list-item-title>
        </v-list-item>

        <pre style="font-size: 12px; white-space: pre-wrap;">
          {{ initDump }}
        </pre>

        <v-list-item>
          <v-list-item-title><strong>initData:</strong> </v-list-item-title>
        </v-list-item>

        <pre style="font-size: 12px; white-space: pre-wrap;">
          {{ initData }}
        </pre>
      </v-list>
    </v-card>
  </v-container>
</template>

<script>
import { ref, onMounted } from "vue";
import { computed } from 'vue';

export default {
  setup() {
    const bot = ref(null);
    const launchUrl = ref(null);
    const urlId = ref(null);
    const platform = ref(null);
    const version = ref(null);
    const user = ref(null);
    const phone = ref(null);
    const chat = ref(null);
    const initData = ref(null);
    const initDump = ref(null);
    const requestingPhone = ref(false);

    const phoneText = computed(() => {
      if (phone.value) return phone.value;
      if (requestingPhone.value) return 'запрашиваю...';
      return 'не получен';
    });

    onMounted(() => {
      const WebApp = window.WebApp;
      const init = WebApp?.initDataUnsafe;
      launchUrl.value = window.location.href;

      const qs = new URLSearchParams(window.location.search);
      urlId.value = qs.get("id");

      if (init) {
        bot.value = init.start_param ?? null;
        user.value = init.user ?? null;
        chat.value = init.chat ?? null;
        initData.value = WebApp?.initData ?? null;
        platform.value = WebApp?.platform ?? null;
        version.value = WebApp?.version ?? null;

        initDump.value = JSON.stringify(init, null, 2);
      }

      // контакт
      if (user.value && !user.value.phone) {
        requestingPhone.value = true;
        WebApp.requestContact()
          .then(res => phone.value = res.phone)
          .finally(() => requestingPhone.value = false);
      } else if (user.value?.phone) {
        phone.value = user.value.phone;
      }

      try {
        WebApp.ready();
      } catch (e) {
        console.warn('WebApp.ready() not supported:', e);
      }

      try {
        WebApp.expand();
      } catch (e) {
        console.warn('WebApp.expand() not supported:', e);
      }
      console.log("initDataUnsafe =", init);
    });

    return {
      bot,
      user,
      chat,
      phone,
      phoneText,
      initData,
      initDump,
      platform,
      version,
      launchUrl,
      urlId 
    };
  }
};
</script>
